import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';

import { IPostFull } from '@ApiClient/backend/Post/post';

import { getPostAction } from '@store/post/post.action';
import { selectPost } from '@store/post/post.selectors';
import { AppStateInterface } from '@store/app/app-state.interface';

@Component({
  selector: 'app-post-page',
  templateUrl: './post.component.html',
  styleUrl: './post.component.scss',
})
export class PostPageComponent implements OnInit {
  postId!: string;
  post$: Observable<IPostFull | null> = this.store.select(selectPost);

  constructor(
    private route: ActivatedRoute,
    public store: Store<AppStateInterface>,
  ) {
    this.route.params.subscribe((params) => {
      this.postId = params['id'];
    });
  }

  ngOnInit() {
    this.store.dispatch(getPostAction({ postId: this.postId }));
  }
}
